@font-face {
  font-family: Century;
  src: url(Fonts/CenturyGothic.ttf);
}

@font-face {
  font-family: Avant;
  src: url(Fonts/AvantGardeRegular.ttf);
}

#logo {
  position: fixed; 
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.8);
  z-index: 0; 
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;  
  height: 100vh; 
  z-index: 1; 
}

body {
  background-color: #ffcc00;
  margin: 0;
}

section{
  color: #000000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  z-index: 2;
}

section {
  min-height: 100vh;
  z-index: 2;
}



main{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}


h1{
  font-family: Avant;
  font-size: 24px;
  font-weight: 600;
  color: #e11f1c;
  padding-bottom: 10px
}

h2{
  font-family: Avant;
  font-size: 24px;
  font-weight: 600;
  color: #000000;
  padding-bottom: 10px
}

.begin {
  grid-column: 5 / 9;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;  
  justify-content: flex-end; 
  text-align: center; 
  padding-bottom: 100px; 
  font-family: Avant;
  font-weight: 600;
}

.center {
  grid-column: 4 / 10;
  padding: 2rem;
  color: rgb(255, 255, 255);
  font-family: Arial, Helvetica, sans-serif;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;    
  justify-content: center; 
  text-align: center;
}


.recipe-frames {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  width: 100%;
  max-width: 800px; 
  margin: 2rem 0;
}

.frame-caption {
  margin: 0;
  padding: 0.5rem;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
}

.recipe-frame {
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  overflow: hidden;
  text-decoration: none;
  background: #e11f1c;
  transition: background-color 0.3s ease;
}

.recipe-frame:hover {
  background-color: #000000; 
  cursor: pointer;
}

.recipe-frame img {
  width: 100%;
  height: 200px; 
  object-fit: cover; 
  display: block;
}

.view-more {
  display: inline-block;
  padding: 0.7rem 1.5rem;
  background-color: #e11f1c;;
  font-family: avant;
  font-weight: 600;
  color: white;
  text-decoration: none;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  text-align: center;
  font-size: 16px;
}

.view-more:hover {
  background-color: black;
}

.left {
  grid-column: 3 / 5;
  padding: 2rem;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;   
  justify-content: center;
  text-align: left;          
}


.right {
  grid-column: 9 / 11;
  padding: 2rem;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
}

.end {
  grid-column: 5 / 9;
  padding: 2rem;
  color: 2a241e;
  font-family: Arial, Helvetica, sans-serif;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  min-height: 100vh; 
  padding-bottom: 50px
}


.end-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;       
  margin-top: 1em; 
}


.end-button {
  font-family: Avant;
  font-weight: 600;
  font-size: 16px;
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: #e11f1c;
  color: white;
  text-decoration: none;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  text-align: center;
}


.end-button:hover {
  background-color: black;
}

.created-by {
  margin-top: 80px;
  font-size: 16px;
  color: #000000;
  text-align: center;
}



header {
  position: fixed;
  color: #e11f1c;
  font-family: Avant;
  font-size: 35px;
  font-weight: 600;
  text-align: left;
  z-index: 120;
  width: 100%;
  height: 90px;
  display: flex;
  justify-content:space-evenly
  
  
}


.header-main-logo{
  width: fit-content;
  height: 100%;
  display: flex; 
}
.header-main-logo img{
  height: 80px;
  align-self: center;
}
.header-main-nav{
  width: fit-content;
  height: 100%;
  display: flex;
  font-family: Avant;
  font-weight: 600; 
  font-size: 14px;
  align-items: center;
}

.header-main-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.header-main-nav ul li{
  display: inline;
  float: left;
  
}
.header-main-nav ul li a{
  padding: 0 20px;
  font-family: Avant;
  line-height: 120px;
  display: block;
  height: 100%;
  
}

.header-main-nav ul li a:hover{
  color: #ffffff;
  
}

a:hover {
  cursor: pointer;
}